@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');

*, html{
  margin: 0;
    padding: 0;
    border: 0;

}
label{
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  height: 50vh;
  width: 50vw;
  margin-top: 20vh;
  margin-left: auto;
  margin-right: auto;
}
html{
  background-image: url("./images/VideoCapture_20230216-120655.jpg");
  width: 100vw;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  
}
.h1{
  font-family: 'Dancing Script', cursive;
  font-size: 150px;
  text-align: center;
  color: #daa520;
  text-shadow: 0 0 16px rgb(20, 20, 20);
}
.Nome{
  color: #FFD700;
  font-size: 24px;
}
label:hover{
  border-radius: 20px;
  background-color: rgba(25, 9, 250, 0.164);
}
input{
  border-radius: 10px;
  width: 40vw;
  height: 35px ;
  border: 1px solid #ffd90000;
  text-align: center;
  font-size: 32px;
  background-color: #ece6e67e;
}

.button{
  width: 12vw;
  font-size: 25px;
  height: 35px ;
  border-radius: 10px;
  color: #051a8b;
  text-align: center;
  background-color: white;
  text-decoration: none;
  
}
.button:hover{
  background-color: #051a8b;
  color: white;
}
img{
  height: 15vh;
  transition: animation 2s;  
  border-radius: 20px;
}

.versiculo{
  color: #000000;
  font-size: 25px;
  text-align: justify;
  background-color: rgba(240, 248, 255, 0.63);
  border-radius: 15px;
}
header{
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-bottom: white 1px solid;
  background: url(./images/gifs-animados-de-brilho-1.gif);
}

@keyframes fotoBrilha {
  0% {
    background: #ffc107b4;
  }
  50% {
    background: url(./images/gifs-animados-de-brilho-1.gif);
  }
  100% {
    background: #ffc1078f;
  }
  
}

.Nome{
  color: white;
  text-align: center;
}
.data{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: #daa520 1px solid;
  border-top: #daa520 1px solid;

}
.date{
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.text{
  padding-top: 50px;
  font-size: 48px;
  color: rgb(250, 250, 250);
  font-family: 'Dancing Script', cursive;
  text-shadow: 0 0 16px rgb(20, 20, 20);
}
.msg{
  font-size: 48px;
  text-shadow: 0 0 16px rgb(20, 20, 20);
  font-family: 'Dancing Script', cursive;
  color: rgb(255, 255, 255);
}
.fundoConvite{
  background-color: #051a8b;
  height: 100vh;
  width: 100vw;
  background-image: url(./images//3f4754b6781554548f2853bda16a02a0.gif);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover ;
}

.links{
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.lin{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-decoration: none;
  color: #daa520  ;
}
.lin:hover{
  background-color: rgba(255, 255, 255, 0.445);
  border-radius: 13px;
}
.numeral{
  font-size: 52px;
  padding-left: 15px;
  padding-right: 15px;
  text-shadow: 0 0 14px #daa520ff;
  font-weight: bolder;
}
.mes{
  text-shadow: 0 0 14px #daa520ff;
  font-weight: bolder;
  font-size: 22px !important;
}

@media (max-width: 600px)
{
  html{
    background-image: url("./images/1677163365781.jpg");
    width: 100vw;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
  }
  .h1{
    font-size: 45px;
    width: 40VW;
    text-align: center;
    text-shadow: 0 0 16px rgb(20, 20, 20);
  }
  label{
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 15px;
    justify-content: center;
    margin-top: 25vh;
  }
  label:hover{
    background-color: rgba(255, 255, 255, 0.219);
    width: 90vw;
  }
  input{
    width: 80vw;
    font-size: 30px;
    text-transform: capitalize;
  }
  .button{
    width: 50vw;
  }
  .versiculo{
    text-align: center;
  }
  header{
    display: none;
  }

  .Nome{
    font-size: 32px;
  }
  .date{
    margin-top: 16px;
  }
  img{
    height: 10vw;
  }
  .fundoConvite{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100vh;
  }
  .text{
    padding-top: 0;
  }
  .nomeCon{
    text-transform: capitalize;
  }
}